import React, {useEffect} from 'react';
import s from './users.module.css';
import photoUsersDefault from '../../img/Users/default-users-photo.png';
import {NavLink, useSearchParams} from "react-router-dom";
import Preloader from "../common/preloader";
import Paginate from "./Paginate";
import Search from "./Search";
import {useDispatch, useSelector} from "react-redux";
import {
    getCurrentPage,
    getFollowingInProgress,
    getIsFetching,
    geTotalUsersCount,
    getPageSize,
    getUsers,
    getUsersFilter
} from "../../redux/users-selectors";
import {filterType, follow, getUsersThunk, onPageChangedThunk, unfollow} from '../../redux/users-reducer';
import {parse} from "querystring";

export const Users = () => {

    const totalUsersCount = useSelector(geTotalUsersCount)
    const currentPage = useSelector(getCurrentPage)
    const pageSize = useSelector(getPageSize)
    const users = useSelector(getUsers)
    const isFetching = useSelector(getIsFetching)
    const followingInProgress = useSelector(getFollowingInProgress)
    const filter = useSelector(getUsersFilter)

    const dispatch: any = useDispatch()
    let [searchParams, setSearchParams] = useSearchParams();

    useEffect(() => {

        const result: any = {}
        // @ts-ignore
        for (const [key, value] of searchParams.entries()) {
            let value2: any = +value
            if (isNaN(value2)) {
                value2 = value
            }
            if (value === 'true') {
                value2 = true
            } else if (value === 'false') {
                value2 = false
            }
            result[key] = value2
        }

        let actualPage = result.page || currentPage
        let term = result.term || filter.term

        let friend = result.friend || filter.friend
        if (result.friend === false) {
            friend = result.friend
        }

        const actualFilter = {friend, term}

        dispatch(getUsersThunk(actualPage, pageSize, actualFilter))

        // eslint-disable-next-line
    }, [])


    useEffect(() => {

        const term = filter.term
        const friend = filter.friend

        let urlQuery =
            (term === '' ? '' : `&term=${term}`)
            + (friend === null ? '' : `&friend=${friend}`)
            + (currentPage === 1 ? '' : `&page=${currentPage}`)

        setSearchParams(urlQuery)

        // eslint-disable-next-line
    }, [filter, currentPage])

    const onPageChanged = (pageNumber: number) => {
        dispatch(onPageChangedThunk(pageNumber, pageSize, filter))
    }
    const onFilterChanged = (filter: filterType) => {
        dispatch(getUsersThunk(1, pageSize, filter))
    }
    const followS = (id: number) => {
        dispatch(follow(id))
    }
    const unfollowS = (id: number) => {
        dispatch(unfollow(id))
    }

    return (<div className={s.users}>
        <h3 className={s.h_title}>Пользователи сети</h3>
        <Search onFilterChanged={onFilterChanged}/>
        {/*Пагинация*/}
        <Paginate currentPage={currentPage} onPageChanged={onPageChanged} totalUsersCount={totalUsersCount}
                  pageSize={pageSize}/>
        {/*Пользователи*/}
        {isFetching ? <Preloader/> :
            users.map((u) => {
                return (<div className={s.user} key={u.id}>
                    <div className={s.title}>
                        <div className={s.photo}>
                            <NavLink to={'/profile/' + u.id}>
                                <img src={u.photos.small != null ? u.photos.small : photoUsersDefault} alt='Картинка'/>
                            </NavLink>
                        </div>
                        <div className={s.following}>
                            {u.followed
                                ? <button disabled={followingInProgress.some(id => id === u.id)} onClick={() => {
                                    unfollowS(u.id)
                                }} className={s.unfollow}>Удалить из друзей</button>
                                : <button disabled={followingInProgress.some(id => id === u.id)} onClick={() => {
                                    followS(u.id)
                                }} className={s.follow}>Добавить в друзья</button>}
                        </div>
                    </div>
                    <div className={s.description}>
                        <div className={s.name}>
                            {u.name}
                        </div>
                        <div className={s.status}>{u.status}</div>
                    </div>
                    <div className={s.location}>
                        <span className={s.country}>
                            {/*{u.location.country}*/}
                        </span>
                        {/*Пробел*/}
                        ,&nbsp;
                        {/*Пробел*/}
                        <span className={s.city}>
                            {/*{u.location.city}*/}
                        </span>
                    </div>
                </div>)
            })}
    </div>);
}
