import React, {FC} from 'react';
import {Field, Formik} from 'formik';
import {filterType} from "../../redux/users-reducer";
import {useSelector} from "react-redux";
import {getUsersFilter} from "../../redux/users-selectors";

type searchType= {
    onFilterChanged: (filter: filterType) => void
}

const Search: FC<searchType> = ({onFilterChanged}) => {
    const filter = useSelector(getUsersFilter)
    return(
        <div>
            <Formik
                initialValues={{term: filter.term, friend: filter.friend}}
                onSubmit={(values: filterType, {setSubmitting}) => {
                    onFilterChanged(values)
                    setSubmitting(false);
                }}
            >
                {({
                      values,
                      handleChange,
                      handleBlur,
                      handleSubmit,
                      isSubmitting,
                      /* and other goodies */
                  }) => (
                    <form onSubmit={handleSubmit}>
                        <input
                            type="text"
                            name="term"
                            onChange={handleChange}
                            onBlur={handleBlur}
                            value={values.term}
                        />
                        <Field name="friend" as="select">
                            <option value="null">Все пользователи</option>
                            <option value="true">Только друзья</option>
                            <option value="false">Только не друзья</option>
                        </Field>
                        <button type="submit" disabled={isSubmitting}>
                            Искать
                        </button>
                    </form>
                )}
            </Formik>
        </div>
    )

}

export default Search;